import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import {
  fetchProductsByFiltersAsync,
  selectAllProducts,
} from "../productSlice";
import { useDispatch, useSelector } from "react-redux";
import { ITEMS_PER_PAGE } from "../../../app/constants";

const Products = ({ wordrobe, desc }) => {
  const dispatch = useDispatch();
  const products = useSelector(selectAllProducts);
  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [page, setPage] = useState(1);
  const [isDragging, setIsDragging] = useState(false);

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchProductsByFiltersAsync({ filter, sort, pagination }));
  }, [dispatch, filter, sort, page]);

  const settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
        },
      },
    ],
    beforeChange: (nextSlide) => {
      const totalSlides = products.length;
      const lastSlideIndex = totalSlides - settings.slidesToShow;

      if (nextSlide >= lastSlideIndex) {
        setShowViewAll(true);
      } else {
        setShowViewAll(false);
      }

      setIsDragging(true);
    },
    afterChange: () => {
      setIsDragging(false);
    },
  };

  const handleLinkClick = (e) => {
    // Prevent the link from being followed if the carousel is being dragged
    if (isDragging) {
      e.preventDefault();
    }
  };

  const [showViewAll, setShowViewAll] = useState(false);

  return (
    <div className="mt-14 mb-12">
      <div className="container">
        {/* Header section */}
        <div className="text-center mb-10 max-w-[600px] mx-auto">
          <p className="text-sm text-primary">Explore Our Most-Loved Items</p>
          <h1 className="text-3xl font-bold">{wordrobe}</h1>
          <p className="text-xs text-gray-400">{desc}</p>
        </div>
        {/* Body section */}
        <div className="relative">
          {/* Add relative positioning */}
          <Slider {...settings} className="your-custom-slider-class">
            {/* card section */}
            {products.map((data) => (
              <Link
                to={`/product-detail/${data.id}`}
                key={data.id}
                className="custom-link"
                onClick={handleLinkClick}
              >
                <div
                  className="space-y-3 custom-product-item  mr-2"
                  key={data.id}
                >
                  <img
                    src={data.thumbnail}
                    alt=""
                    className="h-[320px] w-[200px] object-cover rounded-md"
                  />
                  <div>
                    {/* Use CSS to truncate long titles */}
                    <h3 className="font-semibold overflow-hidden overflow-ellipsis whitespace-nowrap">
                      {data.title}
                    </h3>
                    <div className="flex flex-row items-center space-x-1">
                      <p className="text-sm block font-medium text-gray-900">
                        ₹{data.discountPrice}
                      </p>
                      <p className="text-sm block line-through font-medium text-gray-400">
                        ₹{data.price}
                      </p>
                      <p className="text-sm block font-medium text-green-500 text-lg">{`(${data.discountPercentage}% off)`}</p>
                    </div>
                  </div>
                </div>
              </Link>
            ))}

            {showViewAll && (
              <div className="flex items-center justify-center h-full absolute top-20 w-full  ml-10">
                <div>
                  <Link to={`/home/${"home"}`}>
                    <button className="text-center cursor-pointer text-black relative">
                      View All...
                      <div className="absolute left-0 right-0 bottom-0 h-0.5 bg-black"></div>
                    </button>
                  </Link>
                </div>
              </div>
            )}
          </Slider>
        </div>

        {/* View all button after the first 10 products */}
        {products.length > 10 && (
          <div className="flex justify-center mt-4">
            <Link to={`/home/${"home"}`}>
              <button className="text-center cursor-pointer bg-primary text-white py-1 px-5 rounded-md">
                View All Products
              </button>
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};

export default Products;
